<template>
	<div class="main">
		<div class="header">
			<div class="row w-flex-sm w-flex-md w-flex-lg monthSelect" v-if="selectedLocation.name !== 'Select Location...'">
				<div class="row h-100 w-flex x-center y-center clickable" @click="getPrev">&lt;</div>
				<div class="row h-100 w-flex x-center y-center clickable">{{ months[monthIndex].name }} {{ year }}</div>
				<div class="row h-100 w-flex x-center y-center clickable" @click="getNext">&gt;</div>
			</div>
			<div class="locationSelect">
				<div class="row y-center clickable h-100" @click="showLocationList = !showLocationList">
					<div class="w-flex">{{ selectedLocation.name }}</div>
					<div class="w-10"></div>
				</div>
				<div class="locationList" v-if="showLocationList">
					<div v-for="location in locationList" :key="location['city_id']" class="clickable" @click="selectLocation(location)">{{ location.name }}</div>
				</div>
			</div>
		</div>
		<div class="container" v-if="selectedLocation.name !== 'Select Location...'">
			<div class="column month">
				<div class="row">
					<div class="row w-flex x-center day" v-for="(day, dayIndex) in days" :class="{ 'fc-dark-red' : dayIndex === 0 }" :key="dayIndex">{{ day.substring(0, 3) }}</div>
				</div>
				<div class="row w-flex" v-for="(week, weekIndex) in month" :key="weekIndex">
					<div v-for="(date, dateIndex) in week" :key="dateIndex" :class="{ selectedDate: selectedDate.dateText === date.dateText, today: today.dateText === date.dateText, 'fc-dark-red' : dateIndex === 0 }" class="w-flex row x-center y-center date clickable" :style="(function() {
						thisDate = calDates[selectedLocation['city_id']][date.dateText];
						if (thisDate && thisDate.img) {
							eventBgImg = '../assets/img/' + thisDate.img + '.jpg';
						}
						return styleObject(thisDate)
					})()" @click="selectDate(date, days[dateIndex])">
						{{ date.date }}
						<div class="dot red row x-center y-center" v-if="thisDate && thisDate.ekadashi"></div>
						<div class="dot blue row x-center y-center" v-if="thisDate && thisDate.events && ((thisDate.events.length > 0) || (thisDate.special))">
							{{ thisDate.special ? thisDate.events.length + 1 : thisDate.events.length }}
						</div>
					</div>
				</div>
			</div>
			<div class="events">
				<div class="row events-options margin4 y-center">
					<div class="cancel clickable w-15 h-100 row x-center y-center" @click="toggleClose" v-if="view !== 'selected'">
						<img src="../assets/img/icon/close.png" alt="Close" height="15">
					</div>
					<div class="w-15 clickable h-100 row y-center x-center" @click="goTo(today)" title="Go to Today" v-else>
						<img src="../assets/img/icon/back.png" alt="Today" height="15">
					</div>
					<input type="text" v-model="searchText" id="search" class="w-flex search" placeholder="Search event" @click="view = 'search'">
					<div class="w-15 clickable h-100 row y-center x-center" @click="view = 'all'; filteredEvents = calDates[selectedLocation['city_id']];" title="View all events">
						<img src="../assets/img/icon/view-all.jpg" alt="View All" height="20">
					</div>
				</div>
				<div class="list-container">
					<div v-for="(day, dayIndex) in filteredEvents" :key="dayIndex" class="column w-flex">
						<div v-if="view === 'selected' && filteredEvents[0].dateText === today.dateText && dayIndex == 1" class="fw-bold upcoming row">Upcoming Events</div>
						<div class="list">
							<div class="title row x-left y-center padding8" @click="goTo(day)">
								<div class="w-flex fw-bold">
									<span>{{ day.date }} {{ months[day.month].name }} {{ day.year }}</span>
									<span v-if="day.dateText === today.dateText"> - Today</span>
									<span v-if="day['lunar-day']"> - {{ day['lunar-day'] }}</span>
								</div>
							</div>
							<div>
								<div class="event clickable not special" v-if="day.special">{{ day.special }}</div>
								<div class="event clickable not special11" v-if="day.ekadashi">{{ day.ekadashi }}</div>
								<a v-for="(event, eventIndex) in day.events" :key="eventIndex" :href="event.link" target="_blank" class="event clickable row y-center" :class="{ not: !event.link, 'fw-bold': event.link }">
									<div v-if="event.img" class="event-image" :style="{ 'background-image': 'url(' + require('../assets/img/' + event.img + '.jpg') + ')' }"></div>
									<div>{{ event.name }}</div>
								</a>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
var cities = require('../assets/js/cities');

export default {
	name: 'Calendar',
	data() {
		return {
			view: 'selected',
			month: [],
			monthIndex: 0,
			today: {},
			months: [{ name: 'January', days: 31 }, { name: 'February', days: 28 }, { name: 'March', days: 31 }, { name: 'April', days: 30 }, { name: 'May', days: 31 }, { name: 'June', days: 30 }, { name: 'July', days: 31 }, { name: 'August', days: 31 }, { name: 'September', days: 30 }, { name: 'October', days: 31 }, { name: 'November', days: 30 }, { name: 'December', days: 31 }],
			days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
			dayLastIndex: 0,
			day1Index: 0,
			year: 0,
			selectedDate: { nothing: 0 },
			searchText: '',
			calDates: {},
			selectedLocation: { name: 'Select Location...' },
			showLocationList: false,
			filteredEvents: [],
			upcomingEvents: [],
			locationList: cities
		}
	},
	created() {
		let vm = this;
		let today = new Date();
		vm.today = {
			date: today.getDate(),
			day: today.getDay(),
			month: today.getMonth(),
			year: today.getFullYear() 
		}
		vm.today.dateText = vm.getDateText(vm.today.year, vm.today.month + 1, vm.today.date);
		
		let location = localStorage.location;
		let locationInt = parseInt(location);
		if (locationInt) {
			for (let i = 0; i < cities.length; i++) {
				if(cities[i]['city_id'] == location) {
					vm.selectLocation(cities[i]);
				}
			}
		} else {
			for (let i = 0; i < cities.length; i++) {
				if(cities[i].name == location) {
					vm.selectLocation(cities[i]);
				}
			}
		}
	},
	methods: {
		styleObject(thisDate) {
			let vm = this;
			let mobOpt = vm.$parent.widthLT960 ? '-mob' : '';
			return {
				'--eventBgImg': thisDate && thisDate.img ? 'url(' + require('../assets/img/' + thisDate.img + mobOpt + '.jpg') + ')' : ''
			}
		},
		detectMob() {
			const toMatch = [
				/Android/i,
				/webOS/i,
				/iPhone/i,
				/iPad/i,
				/iPod/i,
				/BlackBerry/i,
				/Windows Phone/i
			];

			return toMatch.some((toMatchItem) => {
				return navigator.userAgent.match(toMatchItem);
			});
		},
		getMonth(date, dateIndex) {
			let vm = this,
				numOfDays = vm.months[vm.monthIndex].days;
			vm.month = [];
			if (date !== 1) {
				vm.day1Index = ((dateIndex - (date % 7)) + 1 + 7) % 7;
			} else {
				vm.day1Index = dateIndex;
			}

			fillMonth(0, 1);

			function fillMonth(weekNo, date) {
				let lastWeek = false;
				vm.month.push([]);
				for (let i = 0; i < 7; i++) {
					let dateText = vm.getDateText(vm.year, vm.monthIndex + 1, date);
					if ((weekNo == 0 && i < vm.day1Index) || lastWeek) {
						vm.month[weekNo].push('');
					} else if (date != numOfDays) {
						vm.month[weekNo].push({ date, month: vm.monthIndex, year: vm.year, dateText: dateText, dayIndex: i });
						date++;
					} else {
						lastWeek = true;
						vm.dayLastIndex = i;
						vm.month[weekNo].push({ date, month: vm.monthIndex, year: vm.year, dateText: dateText, dayIndex: i });
					}
				}
				if (!lastWeek) {
					fillMonth(weekNo + 1, date);
				}
			}
		},
		getDateText(year, month, date) {
			return year + '-' + ('0' + month).slice(-2) + '-' + ('0' + date).slice(-2);
		},
		getPrev() {
			let vm = this;
			if (vm.monthIndex === 0) {
				vm.monthIndex = 11;
				vm.year--;
			} else {
				vm.monthIndex--;
			}
			vm.checkFeb();
			if (vm.day1Index === 0) {
				vm.getMonth(vm.months[vm.monthIndex].days, 6);
			} else {
				vm.getMonth(vm.months[vm.monthIndex].days, vm.day1Index - 1);
			}
		},
		getNext() {
			let vm = this;
			if (vm.monthIndex === 11) {
				vm.monthIndex = 0;
				vm.year++;
			} else {
				vm.monthIndex++;
			}
			vm.checkFeb();
			if (vm.dayLastIndex === 6) {
				vm.getMonth(1, 0);
			} else {
				vm.getMonth(1, vm.dayLastIndex + 1);
			}
		},
		checkFeb() {
			let vm = this;
			if (vm.monthIndex === 1 && (vm.year % 4) === 0) {
				vm.months[1].days = 29;
			} else {
				vm.months[1].days = 28;
			}
		},
		selectDate(date) {
			let vm = this;
			vm.selectedDate = vm.calDates[vm.selectedLocation['city_id']][date.dateText] || date;
			vm.filteredEvents = [vm.selectedDate];
			vm.view = 'selected';
			if (vm.selectedDate.dateText === vm.today.dateText) {
				addUpcoming();
			}

			function addUpcoming() {
				let keys = Object.keys(vm.calDates[vm.selectedLocation['city_id']]), i = -1, date = vm.selectedDate.date + 1, month = vm.selectedDate.month, year = vm.year, count = 0;
				vm.upcomingEvents = [];
				while (i < 0 && count < 60) {
					i = keys.indexOf(vm.getDateText(year, month + 1, date));
					incrementDay();
					count++;
				}
				if (!(count >= 59 && i < 0)) {
					while (i < keys.length && vm.upcomingEvents.length < 7) {
						if (vm.calDates[vm.selectedLocation['city_id']][keys[i]]) {
							vm.upcomingEvents.push(vm.calDates[vm.selectedLocation['city_id']][keys[i]]);
							vm.filteredEvents.push(vm.calDates[vm.selectedLocation['city_id']][keys[i]]);
						}
						i++;
					}
				}

				function incrementDay() {
					if (date < vm.months[month].days) {
						date++;
					} else {
						date = 1;
						if (month < 11) {
							month++
						} else {
							month = 0;
							year++;
						}
					}
				}
			}
		},
		goTo(date) {
			let vm = this;
			if (!date.day) date.day = new Date(date.dateText).getDay();
			if (date.month != vm.monthIndex || date.year != vm.year || vm.month.length < 1) {
				vm.monthIndex = date.month;
				vm.year = date.year;
				vm.checkFeb();
				vm.getMonth(date.date, date.day);
			}
			vm.selectDate(date, date.day, date.dateText);
		},
		selectLocation(location) {
			let vm = this;
			vm.showLocationList = false;
			if (!vm.calDates[location['city_id']]) {
				import('../assets/cal-data/location-' + location['city_id'])
					.then(locationImport => {
						vm.loadLocation(location, locationImport.default);
					})
			} else {
				vm.loadLocation(location);
			}
		},
		loadLocation(location, locationImport) {
			let vm = this;
			vm.selectedLocation = location;
			localStorage.location = location['city_id'];
			if (!vm.calDates[location['city_id']]) {
				for (let j in locationImport) {
					addDetails(locationImport[j], j);
					for (let k = 0; k < locationImport[j].events.length; k++) {
						if (locationImport[j].events[k].img) {
							if (!locationImport[j].img) {
								locationImport[j].img = locationImport[j].events[k].img;
							}
						}
					}
				}
				vm.calDates[location['city_id']] = locationImport;
			}
			if (vm.selectedDate && vm.selectedDate.date) {
				vm.selectDate(vm.selectedDate);
			} else {
				vm.goTo(vm.today);
			}

			function addDetails(date, dateText) {
				date.dateText = dateText;
				let dateTextSplit = dateText.split('-');
				date.year = parseInt(dateTextSplit[0]);
				date.month = parseInt(dateTextSplit[1]) - 1;
				date.date = parseInt(dateTextSplit[2]);
				return date;
			}
		},
		toggleClose() {
			let vm = this;
			vm.searchText = '';
			vm.view = 'selected';
			vm.selectDate(vm.selectedDate);
		}
	},
	watch: {
		searchText() {
			let vm = this,
				keys = Object.keys(vm.calDates[vm.selectedLocation['city_id']]);
			if (vm.searchText != '') {
				vm.filteredEvents = {};
				for (let i = 0; i < keys.length; i++) {
					let date = vm.calDates[vm.selectedLocation['city_id']][keys[i]];
					for (let j = 0; j < date.events.length; j++) {
						if (date.events[j].name.toLowerCase().indexOf(vm.searchText.toLowerCase()) > -1) {
							vm.filteredEvents[keys[i]] = date;
						}
					}
				}
			}
		}
	}
}
</script>
